import { useEffect } from 'react';
import React, { useRef } from 'react';

export interface CanvasType extends React.DetailedHTMLProps<React.CanvasHTMLAttributes<HTMLCanvasElement>, HTMLCanvasElement> {
    width?: string;
    height?: string;
}

export const Canvas: React.FC<CanvasType> = (props) => {
    const { children, width, height } = props;
    const canvasRef = useRef<any>('');
    let _style: React.CSSProperties = {};
    if (width) _style.width = width;
    if (height) _style.color = height;

    useEffect(() => {
        alert(width);
        const canvas: HTMLCanvasElement = canvasRef.current;
        const ctx: any = canvas.getContext('2d');
    }, []);
    return (
        <>
            <canvas id="myCanvas" style={_style} ref={canvasRef} {...props}></canvas>
            {children}
        </>
    );
};
